/** @jsx React.DOM */var Morearty = require('morearty');
var Actions = require('./actions');

var Piece = require('./piece');

var React = require('react');
var $ = require('jquery');

var Game = React.createClass({displayName: 'Game',
  mixins: [Morearty.Mixin],
  render: function() {
    var binding = this.getDefaultBinding();
    if(binding.get('gameOver')){
      return (
        GameOver({binding: binding})
      );
    }else{
      return (
        Board({binding: binding})
      );
    }
  }
});

var Board = React.createClass({displayName: 'Board',
  mixins: [Morearty.Mixin],
  componentDidMount: function() {
    $(document.body).on('keydown', this.handleKeyDown);
  },
  componentWillUnmount: function() {
    $(document.body).off('keydown', this.handleKeyDown);
  },
  handleCompleteTurn: function(){
    Actions.completeTurn();
  },
  handleKeyDown: function(e) {

    var SPACEBAR = 13;
    var ENTER = 32;

    if( e.keyCode === ENTER || e.keyCode === SPACEBAR) {
      e.preventDefault();
      Actions.completeTurn();
    }

  },
  render: function() {

    var binding = this.getDefaultBinding();

    var currentPlayer = binding.get('currentPlayer');
    var canCompleteTurn = binding.get('canCompleteTurn');

    var piecesBinding = binding.sub('pieces');
    var pieces = piecesBinding.get();

    var cellCount = 8;

    var boardRows = [];

    var color = 'white';

    var xPos = 0;
    var yPos = 0;

    for (i = 0; i < cellCount; i++) {
      xPos = 0;

      var boardCells = [];

      //TODO dry up
      if(color === 'white'){
        color = 'black';
      }else{
        color = 'white';
      }

      for (j = 0; j < cellCount; j++) {

        if(color === 'white'){
          color = 'black';
        }else{
          color = 'white';
        }

        // var piece = pieces.filter(piece => ).shift();

        var pos = [xPos, yPos];

        var cell = {
          color: color,
          pos: pos
        }

        var piece = pieces
          .filter(function(p)  {return p.getIn(['pos', 0]) === xPos
              && p.getIn(['pos', 1]) === yPos
              && p.get('captured') !== true;})
          .first();

        var pieceIndex = piecesBinding.get().findIndex(function(p) {
          return p.getIn(['pos', 0]) === xPos
            && p.get('captured') !== true
            && p.getIn(['pos', 1]) === yPos;
        });

        var key = xPos + yPos;

        if(pieceIndex >= 0){
          var binding = piecesBinding.sub(pieceIndex);
          boardCells.push(BoardCell({key: key, cell: cell}, Piece({binding: binding, key: key, currentPlayer: currentPlayer})));
        }else{
          boardCells.push(BoardCell({key: key, cell: cell}));
        }

        xPos++;

      }

      boardRows.push(BoardRow({key: yPos}, boardCells));

      yPos++;

    }

    if (canCompleteTurn) {
      console.log('canCompleteTurn');
      var completeTurn = React.DOM.button({className: "button mb1 bg-fuchsia", onClick: this.handleCompleteTurn}, "Complete turn");
    }

    var redCapturedEnemyPiecesCount = pieces
      .filter(function(piece)  {return piece.get('color') === 'yellow' && piece.get('captured') === true;} )
      .count();

    var yellowCapturedEnemyPiecesCount = pieces
      .filter(function(piece)  {return piece.get('color') === 'red' && piece.get('captured') === true;} )
      .count();

    // var completeTurn = <button onClick={this.handleCompleteTurn}>Complete turn</button>;

    return (
      React.DOM.div(null, 
        React.DOM.table(null, 
          boardRows
        ), 
        React.DOM.div(null, " Turn - ", currentPlayer), 
        completeTurn, 

        React.DOM.div(null, 
          React.DOM.h3(null, "Captured enemy pieces"), 
          React.DOM.div(null, " Red - ", redCapturedEnemyPiecesCount), 
          React.DOM.div(null, " Yellow - ", yellowCapturedEnemyPiecesCount)
        )

      )
    );

  }
});

var GameOver = React.createClass({displayName: 'GameOver',
  mixins: [Morearty.Mixin],
  render: function(){
    var binding = this.getDefaultBinding();
    var winner = binding.get('winner');

    var cx = React.addons.classSet;
    var winnerClasses = cx({
      'red': winner === 'red',
      'yellow': winner === 'yellow'
    });

    return (
      React.DOM.header({className: "center px3 py4 white bg-navy bg-cover bg-center"}, 
        React.DOM.h1({className: "h1 h0-responsive caps mt4 mb0 regular"}, "Game over"), 
        React.DOM.p({className: "h3 caps"}, "Wnner ", React.DOM.span({className: winnerClasses}, winner)), 
        React.DOM.a({href: "#", className: "h3 button button-big mb4"}, "Start again")
      )
    );
  }
});

var BoardRow = React.createClass({displayName: 'BoardRow',
  render: function(){
    return (
      React.DOM.tr({className: "board-row"}, 
        this.props.children
      )
    );
  }
});

var BoardCell = React.createClass({displayName: 'BoardCell',
  handleClick: function(evt) {
    Actions.updatePosition(this.props.cell);
  },
  render: function(){

    var cx = React.addons.classSet;
    var classes = cx({
      'board-cell': true,
      'center': true,
      'bg-navy': this.props.cell.color === 'black',
      'bg-darken-1': this.props.cell.color === 'white'
    });

    var coords;
    // coords = this.props.cell.pos[0] + ',' + this.props.cell.pos[1];
    return (
      React.DOM.td({className: classes, onClick: this.handleClick}, 
        this.props.children, 
        coords
      )
    );

  }
});

module.exports = Game;



